
import iro from './iro.min.js';
import * as styleTransfer from './net';
import * as dl from 'deeplearn';

const canvas = document.getElementById('canvas');
let color = 'rgb(20, 20, 20)'
let style = 'wave'

// Download model
let model = new styleTransfer.TransformNet(style)
model.load().then(() => { console.log('model is loaded') })

// Initialize canvas for drawing
function initDrawing(canvas) {
    var ctx = canvas.getContext('2d');

    var sketch = document.querySelector('#sketch');
    var sketch_style = getComputedStyle(sketch);

    var mouse = { x: 0, y: 0 };
    var last_mouse = { x: 0, y: 0 };

    /* Mouse Capturing Work */
    canvas.addEventListener('mousemove', function (e) {
        last_mouse.x = mouse.x;
        last_mouse.y = mouse.y;

        mouse.x = e.pageX - this.offsetLeft;
        mouse.y = e.pageY - this.offsetTop;
    }, false);

    /* Drawing on Paint App */
    ctx.lineJoin = 'round';
    ctx.lineCap = 'round';

    canvas.addEventListener('mousedown', function (e) {
        const strokeInput = document.getElementById('brush-size')
        const strokeWidth = strokeInput.value
        console.log('stroke width', strokeWidth)
        ctx.strokeStyle = color;
        ctx.lineWidth = strokeWidth;
        canvas.addEventListener('mousemove', onPaint, false);
    }, false);

    canvas.addEventListener('mouseup', function () {
        canvas.removeEventListener('mousemove', onPaint, false);
    }, false);

    var onPaint = function () {
        ctx.beginPath();
        ctx.moveTo(last_mouse.x, last_mouse.y);
        ctx.lineTo(mouse.x, mouse.y);
        ctx.closePath();
        ctx.stroke();
    };
}

// Style image and render to canvas
function generateAndRender(e){
    const ctx = canvas.getContext('2d')
    const file = imgInput.files[0]
    const reader = new FileReader()
    reader.onload = (e) => {
        let dataUrl = e.target.result
        let img = new Image()
        img.src = dataUrl
        img.onload = () => {
            ctx.drawImage(img, 0, 0)
        }
    }
    reader.readAsDataURL(file)
}

function renderToCanvas(a: dl.Tensor3D, canvas: HTMLCanvasElement) {
    const [height, width, ] = a.shape;
    const ctx = canvas.getContext('2d');
    const imageData = new ImageData(width, height);
    const data = a.dataSync();
    for (let i = 0; i < height * width; ++i) {
      const j = i * 4;
      const k = i * 3;
      imageData.data[j + 0] = Math.round(255 * data[k + 0]);
      imageData.data[j + 1] = Math.round(255 * data[k + 1]);
      imageData.data[j + 2] = Math.round(255 * data[k + 2]);
      imageData.data[j + 3] = 255;
    }
    ctx.putImageData(imageData, 0, 0);
}

const STYLE_MAPPINGS: {[varName: string]: string} = {
    'Udnie, Francis Picabia': 'udnie',
    'The Scream, Edvard Munch': 'scream',
    'La Muse, Pablo Picasso': 'la_muse',
    'Rain Princess, Leonid Afremov': 'rain_princess',
    'The Wave, Katsushika Hokusai': 'wave',
    'The Wreck of the Minotaur, J.M.W. Turner': 'wreck'
  };

// Clear canvas
const clearBtn = document.getElementById('clear-button')
clearBtn.onclick = (e) => {
    let ctx = canvas.getContext('2d')
    ctx.clearRect(0, 0, canvas.width, canvas.height)
}

// Trigger stylizing
const styleBtn = document.getElementById('style-button')
styleBtn.onclick = (e) => {
    statusStylingImage()
    const ctx = canvas.getContext('2d')
    const imgdata = ctx.getImageData(0, 0, canvas.width, canvas.height)
    const img = dl.fromPixels(imgdata)
    const generated = model.predict(img)
    renderToCanvas(generated, canvas)
    alert("Done!")
}

// Handle setting canvas background image
const imgInput = document.getElementById('img-upload')
imgInput.addEventListener('change', generateAndRender)

// Colorpicker
let colorWheel = new iro.ColorPicker('#colorWheel', {
    width: 320,
    height: 320,
    padding: 4,
    sliderMargin: 24,
    markerRadius: 8,
    color: "rgb(68, 170, 158)",
    CSS: {} // apply colors to any elements
})

colorWheel.on('color:change', (c) => {
    color = c.rgbString
})

initDrawing(canvas)

function statusStylingImage() {
    alert('Styling image (this can take ~10 seconds)')
}

function download() {
    var dt = canvas.toDataURL('image/jpeg');
    this.href = dt;
};
const downloadBtn = document.getElementById('download-button')
downloadBtn.addEventListener('click', download, false)


const imageIds = [
    'la_muse',
    'rain_princess',
    'scream',
    'udnie',
    'wave',
    'wreck'
]

function addListenerToImages() {
    imageIds.map((imgID) => {
        const img = document.getElementById(imgID)
        img.addEventListener('click', (e) => {
            style = e.target.id
            unCSSAllImages()
            model.setStyle(style)
            model.load()
            borderImage(style)
            console.log(`New style ${style} hase been loaded`)
        })
    })
}

function unCSSAllImages() {
    imageIds.map((imgID) => {
        const img = document.getElementById(imgID)
        img.className = ''
    })
}

function borderImage(imgID) {
    const img = document.getElementById(imgID)
    img.className = 'selectedImage'
}

addListenerToImages()